let acp_querybuilder_template_rule = `
<li id="{{= it.rule_id }}" class="rule-container">
  {{? it.settings.display_errors }}
    <div class="error-container"><i class="{{= it.icons.error }}"></i></div>
  {{?}}
  <div class="rule-filter-container"></div>
  <div class="rule-operator-container"></div>
  <div class="rule-value-container"></div>
  <div class="rule-header">
      <button type="button" data-confirm="rule">
        <i class="dashicons dashicons-yes"></i>
      </button>
      <button type="button" data-delete="rule">
        <i class="dashicons dashicons-no-alt"></i>
      </button>
  </div>
</li>`;

let acp_querybuilder_template_group = `
<div id="{{= it.group_id }}" class="rules-group-container">
  <div class="rules-group-header">
    <div class="group-actions">
      <button type="button" class="button ac-button__add-filter" data-add="rule">
        Add Filter
      </button>
    </div>
    {{? it.settings.display_errors }}
      <div class="error-container"><i class="{{= it.icons.error }}"></i></div>
    {{?}}
  </div>
  <div class=rules-group-body>
    <ul class=rules-list></ul>
  </div>
</div>`;

module.exports = {
	acp_querybuilder_template_rule,
	acp_querybuilder_template_group
};