import Date from "./../types/date";
import Integer from "./../types/integer";
import Ajax from "./../types/ajax";
import Select2Preload from "./../types/select2_preload";
import Select from "./../types/select";

class ACFilterTypes {

	static addType( $class ) {
		if ( !this._types ) {
			this._types = new Map();
		}

		this._types.set( $class.filter_type, $class );
	}

	static getType( $type ) {
		if ( this._types.has( $type ) ) {
			return this._types.get( type )
		}

		return false;
	}

	static getTypes() {
		if ( !this._types ) {
			return new Map();
		}

		return this._types;
	}

}

// Register default types
ACFilterTypes.addType( Date );
ACFilterTypes.addType( Integer );
ACFilterTypes.addType( Ajax );
ACFilterTypes.addType( Select );
ACFilterTypes.addType( Select2Preload );

export default ACFilterTypes;