import Modal from '../../../../../admin-columns/src/js/modules/modal'
import Segments from '../segments'

export default class SegmentModal extends Modal {

	/**
	 *
	 * @param {HTMLElement} el
	 * @param {Segments} segments
	 */
	constructor( el, segments ) {
		super( el );

		this.segments = segments;
		this.form = document.getElementById( 'frm_create_segment' );
		this.formelements = this.form.elements;

		this.initFormEvents();
	}

	initFormEvents() {
		let self = this;

		this.form.addEventListener( 'submit', function( e ) {
			e.preventDefault();
			self.save();
		} );

		this.form.addEventListener( 'keypress', function( e ) {
			if ( e.key === 'Enter' ) {
				e.preventDefault();
				self.form.dispatchEvent( new Event( 'submit' ) );
			}
		} );
	}

	reset() {
		this.displayError( '' );
		this.formelements[ 'segment_name' ].value = '';

		return this;
	}

	onOpen() {
		this.reset();
		let elements = this.form.elements;

		setTimeout( function() {
			elements[ 'segment_name' ].focus();
		}, 100 );

	}

	save() {
		let self = this;
		let name = this.formelements[ 'segment_name' ].value;

		self.setLoading();

		self.segments.saveCurrentSegment( name ).done( function( response ) {
			if ( response.success ) {
				self.segments.addSegment( response.data.segment );
				Segments.pushState( response.data.segment );
				self.segments.setCurrent( name );
				self.reset();
				self.close();

			} else {
				self.displayError( response.data )
			}

			self.setLoading( false );
		} );
	}

	setLoading( on = true ) {
		let loading = this.el.querySelector( '.ac-modal__loading' );

		if ( on ) {
			loading.style.display = 'inline-block';
		} else {
			loading.style.display = 'none';
		}
	}

	displayError( msg ) {
		this.el.querySelector( '.ac-modal__error' ).textContent = msg;
	}

}