import Modal from "./segments/modal";
import {toPixel} from "../helpers/strings"
import {getOffset} from "../helpers/document"
import {removeParameterFromUrl} from "../helpers/url"

const action = 'acp_search_segment_request';

export default class Segments {

	constructor( table, search, sorting ) {
		// Dependencies
		this.Table = table;
		this.Search = search;
		this.Sorting = sorting;

		// Variables
		this.segments = new Map();
		this.button = this.Table.Actions.buttons.querySelector( '.ac-table-button.-segments' );
		this.current = this.button.querySelector( '.ac-table-button__segment__current' ).innerText;

		// Initiation
		this.initSegmentContainer();
		this.initEvents();
		this.checkButtonStatus();

		this._mapSegments();

		// Register Modal
		this.Modal = new Modal( document.querySelector( '#ac-modal-create-segment' ), this );
		AdminColumns.Modals.register(
			this.Modal,
			'create_segment'
		);
	}

	_mapSegments() {
		let self = this;

		self.retrieveSegments().done( ( response ) => {

			response.data.forEach( ( segment ) => {
				self.addSegment( segment );
			} );

			self.setCurrent( this.current );
		} );

	}

	getCurrentState() {
		return {
			rules : JSON.stringify( this.Search.getRules() ),
			sorting : JSON.stringify( this.Sorting )
		}
	}

	unsetCurrent() {
		let url = window.location.href;

		this.setCurrent( '' );
		history.replaceState( {}, '', removeParameterFromUrl( url, 'ac-segment' ) );
	}

	setCurrent( label ) {
		this.current = label;

		let segments = document.querySelectorAll( '.ac-segments__list .ac-segment' );

		for ( let i = 0; i < segments.length; i++ ) {
			let segment = segments[ i ];

			segment.classList.remove( '-current' );

			if ( segment.dataset.name === this.current ) {
				segment.classList.add( '-current' );
			}
		}

		this.button.querySelector( '.ac-table-button__segment__current' ).innerText = label;
	}

	initSegmentContainer() {
		let button = this.button;

		this.container = document.querySelector( '.ac-segments' );

		button.addEventListener( 'open', ( e ) => {
			let offset = getOffset( e.target );

			this.container.style.display = 'block';
			this.container.style.left = toPixel( offset.left + Math.round( e.target.offsetWidth / 2 ) );
			this.container.style.top = toPixel( offset.top );
		} );

		button.addEventListener( 'closed', () => {
			this.container.style.display = 'none';
		} );

	}

	checkButtonStatus() {
		let button = document.querySelector( '.ac-segments__create .button' );
		let enabled = true;
		let state = this.getCurrentState();
		let rules = JSON.parse( state.rules );

		if ( rules.rules.length === 0 ) {
			enabled = false;
		}

		if ( enabled ) {
			button.removeAttribute( 'disabled' );
		} else {
			button.setAttribute( 'disabled', 'disabled' );
		}
	}

	initEvents() {
		let self = this;

		this.container.addEventListener( 'click', ( e ) => {
			e.stopPropagation();
		} );

		this.button.addEventListener( 'click', () => {
			this.checkButtonStatus();
		} );

		this.button.parentElement.addEventListener( 'click', ( e ) => {
			e.stopPropagation();
		} );

		document.addEventListener( 'click', () => {
			if ( self.button.classList.contains( '-open' ) ) {
				jQuery( self.button ).trigger( 'click' );
			}
		} );

		this.container.querySelector( '.button-primary' ).addEventListener( 'click', ( e ) => {
			e.preventDefault();
			AdminColumns.Modals.get( 'create_segment' ).open();
		} );

		this.initInstructions();
	}

	initSegmentEvents( segment ) {
		let self = this;
		let name = segment.dataset.name;

		segment.querySelector( '.ac-segment__delete' ).addEventListener( 'click', ( e ) => {
			e.preventDefault();

			self.delete( name ).done( ( response ) => {
				if ( response.success ) {
					self.removeSegment( name );
				}
			} );

		} );
	}

	addSegment( segment ) {
		let element = document.createElement( "div" );

		element.classList.add( 'ac-segment' );
		element.dataset.name = segment.name;
		element.innerHTML = `<a class="ac-segment__label" href="${segment.url}">${segment.name}</a>
						<button class="ac-segment__delete"><span class="dashicons dashicons-no-alt"></span></button>`;

		this.container.querySelector( '.ac-segments__list' ).insertAdjacentElement( 'beforeend', element );

		this.initSegmentEvents( element );

		return element;
	}

	removeSegment( name ) {
		if ( name === this.current ) {
			this.unsetCurrent();
		}

		this.container.querySelector( `.ac-segments__list [data-name="${name}"]` ).remove();
	}

	delete( name ) {
		return jQuery.ajax( {
			url : ajaxurl,
			method : 'post',
			data : {
				_ajax_nonce : AC.ajax_nonce,
				action : action,
				list_screen : AC.list_screen,
				layout : AC.layout,
				name : name,
				method : 'delete',
			}
		} );
	}

	retrieveSegments() {
		return jQuery.ajax( {
			url : ajaxurl,
			method : 'post',
			data : {
				_ajax_nonce : AC.ajax_nonce,
				action : action,
				list_screen : AC.list_screen,
				layout : AC.layout,
				name : name,
				method : 'read',
			}
		} );
	}

	saveCurrentSegment( name ) {
		return jQuery.ajax( {
			url : ajaxurl,
			method : 'post',
			data : {
				_ajax_nonce : AC.ajax_nonce,
				action : action,
				list_screen : AC.list_screen,
				layout : AC.layout,
				name : name,
				method : 'create',
				rules : JSON.stringify( this.Search.getRules() ),
				order : this.Sorting.order,
				orderby : this.Sorting.orderby
			}
		} );
	}

	initInstructions() {
		// create pointer
		let el = jQuery( '.ac-segments__instructions' );
		let info = jQuery( '#ac-segments-instructions' );
		let timeout = null;

		el.pointer( {
			content : info.html(),
			position : {
				edge : 'left',
				align : 'middle'
			},
			pointerClass : 'ac-wp-pointer wp-pointer wp-pointer-left noclick -nodismiss',
			pointerWidth : 250,
		} );

		el.hover( function() {
			el.pointer( 'open' );
			clearTimeout( timeout );
		}, function() {
			timeout = setTimeout( () => {
				el.pointer( 'close' );
			}, 500 );
		} )
	}

	static pushState( segment ) {
		history.replaceState( {}, segment.name, segment.url );
	}

}