import ACFilterTypes from "./filter-types.js";

export default class ACSearchQuery {

	constructor( query ) {
		this.rules = query.rules;
		this.filters = query.filters;
		this.i18n = query.i18n;
	}

	hasFilters() {
		return this.getFilters().length !== 0;
	}

	init() {
		this.transformFilters( this.filters );
		this.checkRules();
	}

	checkRules() {
		let self = this;

		if ( !this.rules ) {
			return;
		}

		this.rules.rules.forEach( function( rule, index, object ) {
			if ( !self.getFilter( rule.id ) ) {
				object.splice( index, 1 );
			}
		} );
	}

	getFilters() {
		return this.filters;
	}

	getFilter( id ) {
		let filters = this.getFilters();
		for ( let i = 0; i < filters.length; i++ ) {
			let filter = filters[ i ];

			if ( id === filter.id ) {
				return filter;

			}
		}

		return false;
	}

	getRules() {
		if ( !this.rules ) {
			return [];
		}

		return this.rules;
	}

	getRule( index ) {
		let rules = this.getRules().rules;

		if ( !rules ) {
			return false;
		}

		if ( !index in rules ) {
			return false;
		}

		return rules[ index ];
	}

	transformFilters( filters ) {
		let result = [];

		filters.forEach( function( filter ) {

			ACFilterTypes.getTypes().forEach( type => {
				filter = type.transformFilter( filter );
			} );

			result.push( filter );
		} );

		return result;
	}

}