import defaultType from "./default";

export default Object.assign( {}, defaultType, {
	filter_type : 'select',

	/** @param {Rule} rule */
	renderInput( rule ) {
		let filter = rule.getFilter();

		if ( filter.input !== 'select' || filter.use_ajax ) {
			return;
		}

		let settings = {
			width : 150,
			theme : 'acs2'
		};

		if ( Object.keys( filter.values ).length < 10 ) {
			settings.minimumResultsForSearch = -1;
		}

		rule.$el.find( '.rule-value-container select' ).select2( settings );
	},

	transformFilter( filter ) {
		if ( typeof filter.values === 'object' ) {
			filter.input = 'select';
		}

		return filter;
	}

} );