import defaultType from "./default";

export default Object.assign( {}, defaultType, {
	filter_type : 'integer',

	/** @param {Rule} rule */
	renderInput( rule ) {
		let $el1 = rule.$el.find( '.rule-value-container input:first' );
		let $el2 = rule.$el.find( '.rule-value-container input:eq(1)' );

		$el1.on( 'blur change', function() {
			let minvalue = parseInt( jQuery( this ).val() );
			let maxvalue = parseInt( $el2.val() );

			if ( maxvalue < minvalue ) {
				$el2.val( minvalue );
			}
		} );

		$el2.on( 'change', function() {
			let maxvalue = parseInt( jQuery( this ).val() );
			let minvalue = parseInt( $el1.val() );

			if ( minvalue > maxvalue ) {
				$el1.val( maxvalue );
			}

		} );

	},

	transformFilter( filter ) {
		if ( filter.type === 'integer' ) {
			filter.filter_type = this.filter_type;
		}

		if ( filter.type === 'double' ) {
			filter.filter_type = this.filter_type;
			filter.validation = {
				step : 0.01
			}
		}

		return filter;
	}

} );